export const validateUsername = value => !/^[a-zA-Z--]+(([' -][a-zA-Z-- ])?[a-zA-Z--]*)*$/.test(value) ? 'Please Enter valid name ' : null;
export const validateNumber = value => !/\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}/.test(value) ? 'Please Enter valid number ' : null;
